.buy-dialog {
  min-width: 600px;
  .p-dialog-header {
    padding: 5px;
  }
  .buy {
    &__title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      color: #ffffff;
      text-align: center;
    }
  }
  .checkout {
    &__item {
      background: #4d505b;
      border-radius: 8px;
      display: flex;
      color: white;
      padding: 24px;
      justify-content: center;
      margin: 24px 0 36px 0;
    }
    &__total {
      display: flex;
      justify-content: space-between;
      padding: 0 24px 0 0;
      color: white;
    }
    &__info {
      justify-content: center;
      display: flex;
      flex-direction: column;
      div {
        padding-bottom: 11px;
        font-size: 14px;
      }
    }
    &__btn {
      text-align: center;
      .btn {
        padding: 10px 40px;
        background: #2156e1;
        border-radius: 40px;
        display: inline-block;
        color: white;
        font-weight: bold;
      }
    }
    .item {
      &__label {
        font-family: 'Montserrat';
        font-weight: bold;
        font-size: 16px;
        color: #ffd911;
      }
      &__img {
        background: url('../assets/images/b_item.png') center/ 100% 100%
          no-repeat;
        width: 126px;
        height: 115px;
      }
      &__info {
        min-width: 250px;
        padding: 0 37px;
      }
    }
    .info {
      &__name {
        font-size: 17px;
        font-weight: bold;
      }
    }
    .price {
      &__bnb {
        font-weight: bold;
        display: flex;
        &::before {
          content: '';
          background: url('../assets/images/b_bnb_icon.png') center/100% 100%
            no-repeat;
          width: 16px;
          height: 16px;
          display: inline-block;
          margin-right: 8px;
        }
      }
      &__usd {
        padding-left: 24px;
        color: #8a8a8a;
      }
    }
  }
  .success {
    text-align: center;
    color: white;
    &__title {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 600;
      font-size: 32px;
      letter-spacing: 3.2px;
      text-transform: uppercase;
      color: #ffd911;
    }
    &__icon {
      background: url('../assets/images/success-checked.png') center/ 100% 100%
        no-repeat;
      width: 106px;
      height: 106px;
      margin: 59px auto 36px auto;
    }
    &__info {
      margin-bottom: 48px;
      a {
        color: #6297ff;
      }
    }
    &__view-bcs {
      display: flex;
      justify-content: center;
      a {
        color: white;
        text-decoration: underline;
        margin-right: 5px;
      }
      &::after {
        content: '';
        background: url('../assets/images/foreign.png') center/100% 100%
          no-repeat;
        display: inline-block;
        width: 15px;
        height: 15px;
      }
    }
  }

  .add-bnb {
    text-align: center;
    &__desc {
      font-family: 'Montserrat';
      font-size: 18px;
      margin-top: 24px;
      /* identical to box height */
      color: #fff7f7;
    }
    &__options {
      display: flex;
      justify-content: center;
      margin-top: 54px;
    }
    .option {
      color: white;
      border: 1px solid #ffd911;
      box-sizing: border-box;
      border-radius: 8px;
      max-width: 185px;
      padding: 37px;
      cursor: pointer;
      .icon {
        margin: 0 auto;
        width: 40px;
        height: 40px;
      }
      .text {
        font-size: 16px;
        margin-top: 7px;
      }
      &__deposit {
        .icon {
          background: url('../assets/images/money-deposit.png') center/100% 100%
            no-repeat;
        }
        margin-right: 27px;
      }
      &__card {
        .icon {
          background: url('../assets/images/credit-card.png') center/100% 100%
            no-repeat;
        }
        margin-left: 27px;
      }
    }
  }
  .completing {
    text-align: center;
    color: white;
    &__title {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 600;
      font-size: 32px;
    }
    &__img {
      background: url('../assets/images/b_item.png') center/ 100% 100% no-repeat;
      width: 126px;
      height: 115px;
      margin-top: 30px;
      margin-left: 100px;
      margin-bottom: 30px;
    }
    &__loading {
      .p-button {
        background-color: transparent !important;
        color: #ffffff !important;
        opacity: 1;
        position: relative;
        height: 80px;
        width: 80px;
        .p-button-icon {
          position: absolute;
          width: 40px;
          height: 40px;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          border: 3px solid transparent;
          border-top-color: white;
          border-radius: 50%;
        }
        .pi-spin::before {
          display: none;
        }
      }
    }
    &__info {
      margin-top: 30px;
      font-family: 'Montserrat';
      font-size: 24px;
      font-weight: 200;
      margin-bottom: 20px;
    }
  }
  .error {
    color: white;
    &__title {
      font-size: 20px;
      padding-bottom: 20px;
    }
    &__content {
      padding-bottom: 20px;
    }
  }
}

